#home #message {
  height: 80vh;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  color: #325ca3;
  font-size: 1.75em;
  font-weight: 300;
}

#home h1 {
  font-size: 3em;
  margin-bottom: -2vh;
}

#home a {
  color: currentColor;
}

#home #alert {
  font-weight: 400;
  font-size: 1.25rem;
  color: #bb3b50;
}

#home input {
  width: 20vw;
  padding: 10px 10px 0 10px;
  
  font-size: 1.5em;
  text-align: center;

  outline: none;
  
  border: none;
  border-bottom: 2px solid #0193e6;
}

#home button {
  width: 10vw;
  height: 3vh;
  background-color: white;
  color: #325ca3;
  border: 1px solid #325ca3;
  margin-top: 2vh;
}

#home button:hover {
  background-color: #325ca3;
  color: white;
  cursor: pointer;
}